<script>
	import { onMount } from 'svelte';

	onMount(() => {
		new Wonderscroll('h1, small', {
			params: {
				edge: 'end',
				from: 0.25,
				to: 0
			},
			mutators: {
				y: {
					from: 0,
					to: -100,
					ease: 'InQuad'
				},
				scale: {
					from: 1,
					to: 2,
					unit: '',
					ease: 'InQuad'
				},
				color: {
					from: '#fe2b26',
					to: '#264ffe'
				}
			}
		}, {
			debug: true
		});
	});
</script>

